.questions-cont {
    display: flex;
    margin: auto;
}
.question-detail {
    margin-right: 10px;
}
.question-detail > div:first-child {
    display: flex;
}
.question-type {
    text-transform: capitalize;
}
.questions-cont a {
    padding: 3px 4px;
}
.questions-cont h2 {
    display: inline-block;
    margin-left: 15px;
}
.questions-cont img {
    margin-left: 10px;
    max-width: 175px;
}
.questions-cont p {
    margin: 10px 0 10px 17px;
}
.questions-cont button {
    padding: 15px;
    margin: 10px 0;
}