*, *:before, *:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html, body, #root {
  height: 100%;
}
.App {
  display: flex;
  height: 100%;
  width: 100%;
}
button, a {
  cursor: pointer;
}
.el-btn, .rs-play {
  color: #000;
  border: 2px outset #808080;
  border-radius: 5px;
  font-size: inherit;
  text-decoration: none;
  background-color: transparent;
}
.el-btn:active, .rs-play:active {
  border-style: inset;
}